// 配置路由
import Login from '../views/login.vue'
import None from '../views/404.vue'
import Home from '../views/Home.vue'
import Userlist from '../views/Userlist/user/Userlist.vue'
import member from '../views/Userlist/member/member.vue'
import memberlist from '../views/Userlist/member/memberlist.vue'
import changemember from '../views/Userlist/member/changemember.vue'
import user from '../views/Userlist/user/user.vue'
import adduser from '../views/Userlist/user/adduser.vue'
import changeuser from '../views/Userlist/user/changeuser.vue'
import supplierlist from '../views/Userlist/Supplier/supplierlist.vue'
import Machinelist from '../views/Machinelist/Machinelist.vue'
import list from '../views/Machinelist/list.vue'
import addmachine from '../views/Machinelist/addmachine.vue'
import changemachine from '../views/Machinelist/changemachine.vue'
import Goodslist from '../views/Goods/Goodslist.vue'
import Goodsmould from '../views/Goods/Goodsmould.vue'
import Classify from '../views/Goods/Classify.vue'
import Imageroom from '../views/Imageroom/Imageroom.vue'
import Resources from '../views/Authority/Resources.vue'
import Rolelist from '../views/Authority/Role/Rolelist.vue'
import Role from '../views/Authority/Role/Role.vue'
import Changerole from '../views/Authority/Role/Changerole.vue'
import Addrole from '../views/Authority/Role/Addrole.vue'
import Consumer from '../views/Authority/Consumer.vue'
import Brief from '../views/Order/Brief.vue'
import Order from '../views/Order/Order.vue'
let routes = [{
		path: '/login',
		component: Login,
		name: '',
		hidden: true
	},
	{
		path: '/404',
		component: None,
		name: '',
		hidden: true
	},
	{
		path: '/',
		component: Home,
		name: '用户管理',
		iconCls: 'el-icon-message', //图标样式class
		redirect: {
			path: '/userlist'
		},
		children: [
			{
				path: '/userlist',
				component: Userlist,
				name: '用户列表',
			},
			{
				path: '/adduser',
				component: adduser,
				name: '新增用户',
				hidden:true
			},
			{
				path: '/changeuser',
				component: changeuser,
				name: '修改用户',
				hidden:true
			},
			{
				path: '/memberlist',
				component: memberlist,
				name: '会员列表',
			},
			{
				path: '/member/changemember',
				component: changemember,
				name: '修改会员',
				hidden:true
			},
			{
				path: '/supplierlist',
				component: supplierlist,
				name: '供应商列表'
			}
		]
	},
	{
		path: '/',
		component: Home,
		name: '产品',
		iconCls: 'el-icon-share',
		children: [{
				path: '/goodslist',
				component: Goodslist,
				name: '产品列表'
			},
			{
				path: '/goodsmould',
				component: Goodsmould,
				name: '产品模板'
			},
			{
				path: '/classify',
				component: Classify,
				name: '分类设置'
			},
		]
	},
	{
		path: '/',
		component: Home,
		name: '机器管理',
		iconCls: 'el-icon-more',
		children: [
		{
			path: '/list',
			component: list,
			name: '机器列表'
		},
		{
			path: '/addmachine',
			component: addmachine,
			name: '新增机器',
			hidden:true
		},
		{
			path: '/changemachine',
			component: changemachine,
			name: '修改机器',
			hidden:true
		}
		]
	},
	{
		path: '/',
		component: Home,
		name: '图片空间',
		iconCls: 'el-icon-picture',
		children: [{
			path: '/imageroom',
			component: Imageroom,
			name: '图片管理'
		},]
	},
	{
		path: '/',
		component: Home,
		name: '权限管理',
		iconCls: 'el-icon-setting',
		children: [{
			path: '/resources',
			component: Resources,
			name: '资源管理'
		},
		{
			path: '/rolelist',
			component: Rolelist,
			name: '角色列表',
		},
		{
			path: '/addrole',
			component: Addrole,
			name: '新增角色',
			hidden:true
		},
		{
			path: '/changerole',
			component: Changerole,
			name: '修改角色',
			hidden:true
		},
		{
			path: '/consumer',
			component: Consumer,
			name: '用户权限'
		}]
	},
	{
		path: '/',
		component: Home,
		name: '订单管理',
		iconCls: 'el-icon-document',
		children: [{
			path: '/brief',
			component: Brief,
			name: '信息总览'
		},
		{
			path: '/order',
			component: Order,
			name: '订单'
		}]
	},
	{
		path: '*',
		hidden: true,
		redirect: {
			path: '/404'
		}
	}
]

export default routes


