var sha1 = require('sha1');
var header;
if(localStorage.loginSession!=undefined&&localStorage.loginSession!=''){
	var timestamp =new Date().getTime()
	var nonce = Math.random().toString(36).substr(2, 6)
	var sessionId = JSON.parse(localStorage.loginSession).id
	var accessToken = JSON.parse(localStorage.loginSession).accessToken
	var sort = ([accessToken, timestamp, nonce]).sort()
	var signature = sha1(sort[0] + sort[1] + sort[2])
	header = {
		timestamp : timestamp,
		nonce : nonce,
		signature : signature,
		sessionId : sessionId,
		requestId : ''
	}
}
export {header}



