<template>
	<el-row :gutter="20">
	  <el-col :span="12" class="day">
	  	<div class="grid-content bg-purple">
	  		<h5>日报</h5>
		    <el-date-picker
		      v-model="day"
		      type="date"
		      placeholder="选择日期"
		      format="yyyy 年 MM 月 dd 日"
		      value-format="yyyy-MM-dd">
		    </el-date-picker>
		    <div class="daydata">
			    <div class="day_allo">
			    	<p class="title">总金额(元)</p>
		    		<p class="num">{{todaydata.payMoneySum}}</p>
			    </div>
		    	<div class="day_allm">
		    		<p class="title">订单数(个)</p>
		    		<p class="num">{{todaydata.payNum}}</p>
		    	</div>
		    	<div class="day_tm">
			    	<p class="title">退款金额(元)</p>
		    		<p class="num">{{todaydata.refundMoneySum}}</p>
			    </div>
			    <div class="day_to">
			    	<p class="title">退款单数(个)</p>
		    		<p class="num">{{todaydata.refundNum}}</p>
			    </div>
		    </div>
	  	</div>
	  </el-col>
	  <el-col :span="12">
	  	<div class="grid-content bg-purple">
	  		<h5>月报</h5>
	  		<el-date-picker
		      v-model="month"
		      type="month"
		      placeholder="选择月"
		      format="yyyy 年 MM 月"
		      value-format="yyyy-MM">
		   </el-date-picker>
		   <div class="daydata">
		   		<div class="day_allo">
			    	<p class="title">总金额(元)</p>
		    		<p class="num">{{monthdata.payMoneySum}}</p>
			    </div>
		    	<div class="day_allm">
		    		<p class="title">订单数(个)</p>
		    		<p class="num">{{monthdata.payNum}}</p>
		    	</div>
			    <div class="day_tm">
			    	<p class="title">退款金额(元)</p>
		    		<p class="num">{{monthdata.refundMoneySum}}</p>
			    </div>
			    <div class="day_to">
			    	<p class="title">退款单数(个)</p>
		    		<p class="num">{{monthdata.refundNum}}</p>
			    </div>
		    </div>
	  	</div>
	  </el-col>
	  <el-col :span="12" class="day">
	  	<div class="grid-content bg-purple">
	  		<h5>期报</h5>
		    <el-date-picker
		      v-model="interval"
		      type="daterange"
		      range-separator="至"
		      start-placeholder="开始日期"
		      end-placeholder="结束日期"
		      format="yyyy 年 MM 月 dd 日"
		      value-format="yyyy-MM-dd">
		    </el-date-picker>
		    <div class="daydata">
			    <div class="day_allo">
			    	<p class="title">总金额(元)</p>
		    		<p class="num">{{intervaldata.payMoneySum}}</p>
			    </div>
		    	<div class="day_allm">
		    		<p class="title">订单数(个)</p>
		    		<p class="num">{{intervaldata.payNum}}</p>
		    	</div>
			    <div class="day_tm">
			    	<p class="title">退款金额(元)</p>
		    		<p class="num">{{intervaldata.refundMoneySum}}</p>
			    </div>
			    <div class="day_to">
			    	<p class="title">退款单数(个)</p>
		    		<p class="num">{{intervaldata.refundNum}}</p>
			    </div>
		    </div>
	  	</div>
	  </el-col>
	</el-row>
</template>

<script>
	import {header} from '../../apis/header.js'
	import config from '../../config.js'
	export default {
		data() {
			return {
				day:"",
				month: "",
				interval:"",
				todaydata:{},
				monthdata:{},
				intervaldata:{}
			}
		},
		methods:{
			add0(m) {
				return m < 10 ? '0' + m : m
			},
			gettoday(){
				this.day=new Date().getFullYear()+"-"+this.add0(new Date().getMonth()+1)+"-"+this.add0(new Date().getDate())
				this.month=new Date().getFullYear()+"-"+this.add0(new Date().getMonth()+1)
			},
			gettoaydata(){
				let url=config.apiUrl+"/data/orderInfoOverView?timeStart="+this.day+"&dateUnit=d"
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.todaydata=data.data
				})
			},
			getmonthdata(){
				let url=config.apiUrl+"/data/orderInfoOverView?timeStart="+this.month+"&dateUnit=m"
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.monthdata=data.data
				})
			},
			getInterval(){
				console.log(this.interval[0])
				let url=config.apiUrl+"/data/orderInfoOverView?timeStart="+this.interval[0]+"&timeEnd="+this.interval[1]+"&dateUnit=c"
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.intervaldata=data.data
				})
			}
		},
		created() {
			this.gettoday()
			this.gettoaydata()
			this.getmonthdata()
		},
		watch: {
		    // 如果 `question` 发生改变，这个函数就会运行
		    day:function(){
		    	this.gettoaydata()
		    },
		   	month:function(){
		    	this.getmonthdata()
		   	},
		   	interval:function(){
		   		this.getInterval()
		   	}
		}
	}
</script>

<style lang="less" scoped="scoped">
	.grid-content{
		font-size: 20px;
		margin-top: 20px;
		h5{
			margin: 10px 0;
		}
	}
	.day{
		
	}
	.daydata{
		width: 100%;
		div{
			display: inline-block;
			width: 44%;
			max-width: 200px;
			height: 150px;
			float: left;
			margin-top: 15px;
			font-size: 15px;
			margin-right: 3%;
		}
	}
	.title{
		height: 30px;
		text-align: center;
	}
	.num{
		height: 120px;
		text-align: center;
		line-height: 120px;
		font-size: 22px;
		border: 1px solid #CCCCCC;
		border-radius: 3px;
	}
</style>


