<template>
	<div>
		<section v-show='show'>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="keyword" placeholder="用户名/邮箱"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getuserlist()">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="userlist" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" width="80">
			</el-table-column>
			<el-table-column prop="loginName" label="用户名" min-width="100" >
			</el-table-column>
			<el-table-column prop="roleName" label="角色名" min-width="120" >
			</el-table-column>
			<el-table-column prop="gender" label="性别" width="100">
				<template slot-scope="scope">
			        {{ scope.row.gender==1 ? '男' : '女' }}
			    </template>
			</el-table-column>
			<el-table-column prop="email" label="邮箱" min-width="150">
			</el-table-column>
			<el-table-column prop="createTime" label="创建时间" min-width="150" sortable>
				<template slot-scope="scope">
			        {{timeFormat(scope.row.createTime)}}
			    </template>
			</el-table-column>
			<el-table-column prop="status" label="账号状态" width="100">
				<template slot-scope="scope">
			        {{status(scope.row.status)}}
			    </template>
			</el-table-column>
			<el-table-column label="操作" width="220" align='center'>
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
					<!--<el-button type="small" size="small">详情</el-button>-->
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
	</div>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				keyword:'',
				mapsearch:'',
				mapshow: false,
				shangjiaid: '',
				show: true,
				showadd: false,
				showedit: false,
				show_merchantId: false,
				filters: {
					name: ''
				},
				userlist: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [], //列表选中列

				editFormVisible: false, //编辑界面是否显示
				editLoading: false,
				editFormRules: { /*修改时必填的项目*/
					name: [{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					}]
				},
				addFormVisible: false, //新增界面是否显示
				addLoading: false,
				addFormRules: {
					name: [{
						required: true,
						message: '请输入机器名',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			handleCurrentChange(val) {
				this.page = val;
				this.getuserlist();
			},
			//时间戳转化成时间格式
			add0(m) {
				return m < 10 ? '0' + m : m
			},
			timeFormat(timestamp) {
				var time = new Date(timestamp);
				var year = time.getFullYear();
				var month = time.getMonth() + 1;
				var date = time.getDate();
				var hours = time.getHours();
				var minutes = time.getMinutes();
				return year + '.' + this.add0(month) + '.' + this.add0(date) + ' ' + this.add0(hours) + ':' + this.add0(minutes);
			},
			status(statu){
				if(statu==0){
					return "禁用"
				}else if(statu==1){
					return "正常"
				}else{
					return ""
				}
			},
			//获取用户列表
			getuserlist() {
				this.listLoading = true;
				let url =config.apiUrl+'/user/list?page='+this.page+'&pageSize=10'+'&deleted=false'+'&keyword='+this.keyword
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						this.userlist = data.data.list
						this.total=data.data.total
						this.listLoading = false;
					})
			},
			//删除
			handleDel(index, row) {
				this.$confirm('确认删除本用户吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url = config.apiUrl + '/user'
					this.$http({
						method:'delete',
						url:url,
						data:{"ids":[row.id]},
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					}).then((data) => {
						if(data.data.errCode==undefined){
							this.$message({
								message: '删除成功',
								type: 'success'
							})
							this.getuserlist() /*重新获取用户列表*/
						}else{
							this.$message({
								message: data.data.errMsg,
								type: 'warning'
							})
							this.getuserlist()
						}
					}).catch((err) => {
						console.log('错误')
					})
				}).catch(() => {
					return
				});

			},
			//跳转编辑界面
			handleEdit: function(index, row) {
				this.$router.push({
					path: '/changeuser',
					name: '修改用户',
					query: {
						id:row.id
					}
				})
			},
			//跳转新增界面
			handleAdd: function() {
				this.$router.push({
					path: '/adduser'
				})
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function() {
				var ids = this.sels.map(item => item.id );
				this.$confirm('确认删除选中用户吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					console.log(para)
					let url = config.apiUrl + '/user'
					this.$http({
						method: 'delete',
						url: url,
						data: para,
						header: {
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then((data) => {
						if(data.data.errCode==undefined){
							this.$message({
								message: '删除成功',
								type: 'success'
							})
							this.getuserlist() /*重新获取用户列表*/
						}else{
							this.$message({
								message: data.data.errMsg,
								type: 'warning'
							})
							this.getuserlist() /*重新获取用户列表*/
						}
					})

				}).catch(() => {

				});
			},
			openmap(){
				let that=this
				this.mapshow=true
				let mapbtn = document.getElementById('mapbtn')
				let map = document.getElementById('map')
				map = new BMap.Map("map")
				map.centerAndZoom(new BMap.Point(113.943214,22.529155),17);
				map.enableScrollWheelZoom();
			    map.enableKeyboard();
			    map.enableDragging();
			    map.enableDoubleClickZoom()
			    map.addEventListener('click',function(e){
			    	that.addForm.lng=e.point.lng
			    	that.addForm.lat=e.point.lat
			    })
			    let local = new BMap.LocalSearch(map, {
					renderOptions:{map: map}
				})
			    mapbtn.addEventListener('click',function(){
			    	if(that.mapsearch!=''){
					  local.search(that.mapsearch)
					}
			    })
			},
			merchantId(){
				this.show_merchantId=true
			}
		},
		created() {
			this.getuserlist();
		}
	}
</script>

<style scoped>
#map{
	width: 600px;
	height: 400px;
}
.show_merchantId{
	width: 50%;
	height: 200px;
	background-color: white;
	border: 1px solid #bfcbd9;
	border-radius: 5px;
	margin-left: 100px;
	position: absolute;
	top: 100px;
	z-index: 99;
}
</style>


