<template>
	<div>
		<section v-show='show'>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="keyword" placeholder="机器编号/机器名"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getmachine()">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="machinelist" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" width="80">
			</el-table-column>
			<!--<el-table-column prop="id" label="ID" width="100" sortable>
			</el-table-column>-->
			<el-table-column prop="machineNo" label="机器编号" width="140">
			</el-table-column>
			<el-table-column prop="name" label="机器名" width="140">
			</el-table-column>
			<el-table-column prop="address" label="地址" min-width="140">
			</el-table-column>
			<el-table-column prop="goodsStock" label="米余量" width="120" sortable>
			</el-table-column>
			<el-table-column prop="packageStock" label="包装余量" width="120" sortable>
			</el-table-column>
			<!--<el-table-column prop="merchantId" label="合作商ID" min-width="100" sortablealign='center'>
			</el-table-column>-->
			<el-table-column label="操作" width="220" align='center'>
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
					<el-button type="small" size="small">发命令</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
		<!--修改機器-->
		
	</div>
</template>

<script>
	import {header} from '../../apis/header.js'
	import config from '../../config.js'
	export default {
		data() {
			return {
				keyword:'',
				mapsearch:'',
				mapshow: false,
				shangjiaid: '',
				show: true,
				showadd: false,
				showedit: false,
				show_merchantId: false,
				filters: {
					name: ''
				},
				machinelist: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [], //列表选中列

				editFormVisible: false, //编辑界面是否显示
				editLoading: false,
				editFormRules: { /*修改时必填的项目*/
					name: [{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					}]
				},
				addLoading: false,
				addFormRules: {
					name: [{
						required: true,
						message: '请输入机器名',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			handleCurrentChange(val) {
				this.page = val;
				console.log(val)
				this.getmachine();
			},
			//获取机器列表
			getmachine() {
				this.listLoading = true;
				let url = config.apiUrl+'/machine/list?page='+this.page+'&pageSize=10'+'&deleted=false&keyword='+this.keyword
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						this.machinelist = data.data.list
						this.total=data.data.total
						this.listLoading = false;
					})
			},
			//删除
			handleDel(index, row) {
				this.$confirm('确认删除本机器吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url = config.apiUrl + '/machine'
					this.$http({
						method:'delete',
						url:url,
						data:{"ids":[row.id]},
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					}).then((data) => {
						if(data.data.errCode==undefined){
							this.$message({
								message: '删除成功',
								type: 'success'
							})
							this.getmachine() /*重新获取用户列表*/
						}else{
							this.$message({
								message: data.data.errMsg,
								type: 'warning'
							})
							this.getmachine()
						}
					}).catch((err) => {
						console.log('错误')
					})
				}).catch(() => {
					return
				});

			},
			//显示编辑界面
			handleEdit: function(index, row) {
				this.$router.push({
					path: '/changemachine',
					name: '修改机器',
					query: {
						machineNo:row.machineNo
					}
				})
			},
			//显示新增界面
			handleAdd: function() {
				this.$router.push({
					path: '/addmachine'
				})
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function() {
				var ids = this.sels.map(item => item.id );
				this.$confirm('确认删除选中机器吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					console.log(para)
					let url = config.apiUrl + '/machine'
					this.$http({
						method: 'delete',
						url: url,
						data: para,
						header: {
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then((data) => {
						if(data.data.errCode==undefined){
							this.$message({
								message: '删除成功',
								type: 'success'
							})
							this.getmachine() /*重新获取机器列表*/
						}else{
							this.$message({
								message: data.data.errMsg,
								type: 'warning'
							})
							this.getmachine() /*重新获取机器列表*/
						}
					})

				}).catch(() => {

				});
			},
			merchantId(){
				this.show_merchantId=true
			}
		},
		created() {
			this.getmachine();
		}
	}
</script>

<style scoped>
#map{
	width: 600px;
	height: 400px;
}
.show_merchantId{
	width: 50%;
	height: 200px;
	background-color: white;
	border: 1px solid #bfcbd9;
	border-radius: 5px;
	margin-left: 100px;
	position: absolute;
	top: 100px;
	z-index: 99;
}
</style>


