<template>
	<section style="font-size: 16px;">
			<el-form :inline="true" :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<el-form-item label="名称" prop="name">
					<el-input v-model="editForm.name"></el-input>
				</el-form-item>
				<el-form-item label="类型" prop="type">
					<el-input v-model="editForm.type" auto-complete="off" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="商家" prop="merchantId">
					<el-input v-model="editForm.merchantId" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="备注" prop="remark">
					<el-input v-model="editForm.remark" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click="editSubmit()" :loading="editLoading">修改</el-button>
				<el-button @click="quxiao()">取消</el-button>
			</div>
		</section>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				editForm: {
					"name": "",
					"type": "",
					"merchantId": "",
					"remark": ''
				},
				editFormRules: { /*修改时必填的项目*/
					name: [{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					}]
				},
				editLoading: false,
				id:''
			}
		},
		methods: {
			/*获取路有参数*/
			getParams () {
		        this.id = this.$route.query.id
			},
			/*获取角色信息*/
			handleEdit(id) {
				let url = config.apiUrl+'/role/detail/' + id
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						this.editForm = data.data.role
					})
			},
			editSubmit(){
				this.$confirm('确认修改本角色吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url=config.apiUrl+'/role'
					this.$http({
						method:'put',
						url:url,
						data:this.editForm,
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then(data=>{
						if(data.data.errCode==undefined){
							this.$alert('修改角色成功', '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									path: '/rolelist'
								})
					          }
					        });
						}else{
							this.$alert(data.data.errMsg, '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									path: '/rolelist'
								})
					          }
					        });
						}
					})
				}).catch(() => {
					return
				});
			},
			quxiao(){
				this.$alert('取消修改', '提示', {
				    confirmButtonText: '确定',
				    callback: action => {
				        this.$router.push({
							path: '/rolelist'
						})
				    }
				});
			}
		},
		computed: {
			
		},
		created(){
            this.handleEdit(this.$route.query.id)
       },
		watch: {
		// 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
		    '$route': 'getParams'
		}
	}
</script>

<style>
</style>


