<template>
	<section style="font-size: 16px;">
			<!--<strong>新增用户</strong><--><br />
			<el-form :inline="true" :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="名称" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="类型" prop="type">
					<el-input v-model="addForm.type" auto-complete="off" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="商家" prop="merchantId">
					<el-select v-model="addForm.merchantId" placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="备注" prop="remark ">
					<el-input v-model="addForm.remark " auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click.native="newUser()" :loading="addLoading">提交</el-button>
				<el-button @click.native="callback()">取消</el-button>
			</div>
		</section>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				'addForm': {
					"name": "",
					"type": "SYSTEM_DEFAULT",
					"merchantId": "",
					"remark ": ""
				},
				options: [],
				mapshow: false,
				mapsearch:'',
				addLoading: false,
				show_merchantId: false,
				addFormRules: {
					loginName: [{
						required: true,
						message: '请输入用户名',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '请输入密码',
						trigger: 'blur'
					}],
					roleId: [{
						required: true,
						message: '请选择角色',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			getRoleId(){
				let url=config.apiUrl+'/merchant/list?page=1&pageSize=100'
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.options=data.data.list
				})
			},
			newUser(){
				this.$confirm('确认添加本角色吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url=config.apiUrl+'/role'
					this.$http({
						method:'post',
						url:url,
						data: this.addForm,
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then(data=>{
						if(data.data.errCode==undefined){
							this.$alert('新角色添加成功', '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									path: '/rolelist'
								})
					          }
					        });
						}else{
							this.$alert(data.data.errMsg, '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            
					          }
					        })
						}
					})
				}).catch(() => {
					return
				});
			},
			callback(){
				this.$alert('确认取消添加', '提示', {
				          confirmButtonText: '确定',
				          callback: action => {
				            this.$router.push({
								path: '/rolelist'
							})
				          }
				        });
			}
		},
		created() {
			this.getRoleId();
		}
	}
</script>

<style scoped="scoped">
#map{
	width: 600px;
	height: 400px;
}
.show_merchantId{
	width: 50%;
	height: 200px;
	background-color: white;
	border: 1px solid #bfcbd9;
	border-radius: 5px;
	margin-left: 100px;
	position: absolute;
	top: 100px;
	z-index: 99;
}
</style>


