<template>
	<section style="font-size: 16px;">
			<el-form :inline="true" :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<!--<el-form-item label="ID" prop="id">
					<el-input v-model="editForm.id" :disabled="true"></el-input>
				</el-form-item>-->
				<el-form-item label="用户名" prop="loginName">
					<el-input v-model="editForm.loginName"></el-input>
				</el-form-item>
				<el-form-item label="角色" prop="roleId">
					<el-select v-model="editForm.roleId" placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="商家ID" prop="merchantId">
					<!--<el-input v-model="editForm.merchantId " auto-complete="off"></el-input>-->
					<el-select v-model="editForm.merchantId" placeholder="请选择">
					    <el-option
					      v-for="item in options1"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="姓名" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="账户状态" prop="status">
					<el-select v-model="editForm.status" placeholder="请选择">
					    <el-option
					      v-for="item in options2"
					      :key="item.status"
					      :label="item.value"
					      :value="item.status">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="身份证号" prop="idNumber">
					<el-input v-model="editForm.idNumber" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="手机号码" prop="mobile">
					<el-input v-model="editForm.mobile" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
					<el-input v-model="editForm.email" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="品牌商ID" prop="brandBusinessId">
					<el-input v-model="editForm.brandBusinessId" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="微信号ID" prop="wechatId">
					<el-input v-model="editForm.wechatId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="微信号" prop="unionId">
					<el-input v-model="editForm.unionId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="省" prop="province">
					<el-input v-model="editForm.province" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="市" prop="city">
					<el-input v-model="editForm.city" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="详细地址" prop="address">
					<el-input v-model="editForm.address" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="出生日期" prop="birthday">
					<!--<el-input v-model="editForm.birthday" auto-complete="off"></el-input>-->
					<el-date-picker
				      v-model="editForm.birthday"
				      type="date"
				      placeholder="选择日期">
				    </el-date-picker>
				</el-form-item>
				<el-form-item label="性别" prop="gender">
					<el-input v-model="editForm.gender==1 ? '男' : '女'  " auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click="editSubmit()" :loading="editLoading">修改</el-button>
				<el-button @click="quxiao()">取消</el-button>
			</div>
		</section>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				options:[],
				options1:[],
				options2:[{'status':0,'value':'禁用'},{'status':1,'value':'正常'}],
				editForm: {},
				editFormRules: { /*修改时必填的项目*/
					loginName: [{
						required: true,
						message: '请输入用户名',
						trigger: 'blur'
					}]
				},
				editLoading: false,
				id:''
			}
		},
		methods: {
			/*获取路有参数*/
			getParams () {
		        this.id = this.$route.query.id
			},
			/*获取角色信息*/
			handleEdit(id) {
				let url = config.apiUrl+'/user/' + id
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						console.log(data.data)
						this.editForm = data.data
					})
			},
			editSubmit(){
				this.$confirm('确认修改本用户吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url=config.apiUrl+'/user'
					this.$http({
						method:'put',
						url:url,
						data:this.editForm,
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then(data=>{
						if(data.data.errCode==undefined){
							this.$alert('修改用户成功', '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									path: '/userlist'
								})
					          }
					        });
						}else{
							this.$alert(data.data.errMsg, '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									
								})
					          }
					        });
						}
					})
				}).catch(() => {
					return
				});
			},
			quxiao(){
				this.$alert('取消修改', '提示', {
				    confirmButtonText: '确定',
				    callback: action => {
				        this.$router.push({
							path: '/userlist'
						})
				    }
				});
			},
			getoptions(){
				let url=config.apiUrl+'/role/list?page=1&pageSize=100'
				let url1=config.apiUrl
				this.$http({
					mehtod:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.options=data.data.list
				})
				
			},
			getoptions1(){
				let url=config.apiUrl+'/merchant/list?page=1&pageSize=100'
				let url1=config.apiUrl
				this.$http({
					mehtod:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.options1=data.data.list
				})
				
			}
		},
		computed: {
			
		},
		created(){
            this.handleEdit(this.$route.query.id)
            this.getoptions()
            this.getoptions1()
       },
		watch: {
		// 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
		    '$route': 'getParams'
		}
	}
</script>

<style>
</style>


