<template>
	<section style="font-size: 16px;">
			<!--<strong>新增用户</strong><--><br />
			<el-form :inline="true" :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="用户名" prop="loginName">
					<el-input v-model="addForm.loginName" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="密码" prop="password">
					<el-input v-model="addForm.password" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="角色" prop="roleId">
					<el-select v-model="addForm.roleId" placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="姓名" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="身份证号" prop="idNumber">
					<el-input v-model="addForm.idNumber" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="手机号码" prop="mobile">
					<el-input v-model="addForm.mobile" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
					<el-input v-model="addForm.email" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="品牌商ID" prop="brandBusinessId">
					<el-input v-model="addForm.brandBusinessId" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="微信号ID" prop="wechatId">
					<el-input v-model="addForm.wechatId" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="微信号" prop="unionId">
					<el-input v-model="addForm.unionId" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="省" prop="province">
					<el-input v-model="addForm.province" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="市" prop="city">
					<el-input v-model="addForm.city" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="详细地址" prop="address">
					<el-input v-model="addForm.address" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="商家ID" prop="merchantId">
					<el-input v-model="addForm.merchantId" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="出生日期" prop="birthday">
					<el-date-picker
				      v-model="addForm.birthday"
				      type="date"
				      placeholder="选择日期">
				    </el-date-picker>
				</el-form-item>
				<el-form-item label="性别" prop="gender">
					<template>
					  <el-radio v-model="addForm.gender" label="1">男</el-radio>
					  <el-radio v-model="addForm.gender" label="2">女</el-radio>
					</template>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click.native="newUser()" :loading="addLoading">提交</el-button>
				<el-button @click.native="callback()">取消</el-button>
			</div>
		</section>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				'addForm': {
					"loginName": "",
					"password": "",
					"name": "",
					"gender": "",
					"birthday ": "",
					"mobile ": "",
					"email": "",
					"idNumber ":"",
					"brandBusinessId ": "",
					"wechatId ": "",
					"unionId ": "",
					"province ": "",
					"city": "",
					"address": '',
					"roleId": "",
					"merchantId ":""
				},
				options: [],
				mapshow: false,
				mapsearch:'',
				addLoading: false,
				show_merchantId: false,
				addFormRules: {
					loginName: [{
						required: true,
						message: '请输入用户名',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '请输入密码',
						trigger: 'blur'
					}],
					roleId: [{
						required: true,
						message: '请选择角色',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			getRoleId(){
				let url=config.apiUrl+'/role/list?page=1&pageSize=100'
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.options=data.data.list
				})
			},
			newUser(){
				this.$confirm('确认添加本用户吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url=config.apiUrl+'/user'
					this.$http({
						method:'post',
						url:url,
						data: this.addForm,
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then(data=>{
						if(data.data.errCode==undefined){
							this.$alert('新用户添加成功', '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									path: '/userlist'
								})
					          }
					        });
						}else{
							this.$alert(data.data.errMsg, '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            
					          }
					        });
						}
					})
				}).catch(() => {
					return
				});
			},
			callback(){
				this.$alert('确认取消添加', '提示', {
				          confirmButtonText: '确定',
				          callback: action => {
				            this.$router.push({
								path: '/userlist'
							})
				          }
				        });
			}
		},
		created() {
			this.getRoleId();
		}
	}
</script>

<style scoped="scoped">
#map{
	width: 600px;
	height: 400px;
}
.show_merchantId{
	width: 50%;
	height: 200px;
	background-color: white;
	border: 1px solid #bfcbd9;
	border-radius: 5px;
	margin-left: 100px;
	position: absolute;
	top: 100px;
	z-index: 99;
}
</style>


