<template>
	<div>
		<section v-show='show'>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="keyword" placeholder="用户名/手机号码"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getmemberlist()">查询</el-button>
				</el-form-item>
				<!--<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>-->
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="userlist" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" width="80">
			</el-table-column>
			<el-table-column prop="username" label="昵称" width="120" >
			</el-table-column>
			<el-table-column prop="phone" label="手机号码" width="120">
			</el-table-column>
			<el-table-column prop="vip" label="VIP" width="100">
				<template slot-scope="scope">
			        {{ scope.row.vip==1 ? '是' : '否' }}
			    </template>
			</el-table-column>
			<!--<el-table-column prop="id" label="uesrId" min-width="120">
			</el-table-column>-->
			<el-table-column prop="score" label="积分" width="120" sortable>
			</el-table-column>
			<el-table-column prop="percentage" label="提成" width="120" sortable>
			</el-table-column>
			<el-table-column prop="status" label="用户状态" min-width="100" sortablealign='center'>
				<template slot-scope="scope">
			        {{ scope.row.status==1 ? '正常' : '异常' }}
			    </template>
			</el-table-column>
			<el-table-column label="操作" width="220" align='center'>
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
					<!--<el-button type="small" size="small">详情</el-button>-->
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
	</div>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				keyword:'',
				mapsearch:'',
				mapshow: false,
				shangjiaid: '',
				show: true,
				showadd: false,
				showedit: false,
				show_merchantId: false,
				filters: {
					name: ''
				},
				userlist: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [], //列表选中列
				editFormRules: { /*修改时必填的项目*/
					name: [{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					}]
				},
				addFormVisible: false, //新增界面是否显示
				addLoading: false,
				addFormRules: {
					name: [{
						required: true,
						message: '请输入机器名',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			handleCurrentChange(val) {
				this.page = val;
				this.getmemberlist();
			},
			//获取会员列表
			getmemberlist() {
				this.listLoading = true;
				let url =config.apiUrl+'/member/list?page='+this.page+'&pageSize=10'+'&deleted=false'+'&keyword='+this.keyword
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						this.userlist = data.data.list
						this.total=data.data.total
						this.listLoading = false;
					})
			},
			//删除
			handleDel(index, row) {
				this.$confirm('确认删除此会员吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url = config.apiUrl + '/member'
					this.$http({
						method:'delete',
						url:url,
						data:{"ids":[row.id]},
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					}).then((data) => {
						if(data.data.errCode==undefined){
							this.$message({
								message: '删除成功',
								type: 'success'
							})
							this.getmemberlist() /*重新获取用户列表*/
						}else{
							this.$message({
								message: data.data.errMsg,
								type: 'warning'
							})
							this.getmemberlist()
						}
					}).catch((err) => {
						console.log('错误')
					})
				}).catch(() => {
					return
				});

			},
			//显示编辑界面
			handleEdit: function(index, row) {
				this.$router.push({
					path: '/changemember',
					name: '修改会员',
					query: {
						id:row.id
					}
				})
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function() {
				var ids = this.sels.map(item => item.id );
				this.$confirm('确认删除选中会员吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					console.log(para)
					let url = config.apiUrl + '/member'
					this.$http({
						method: 'delete',
						url: url,
						data: para,
						header: {
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then((data) => {
						if(data.data.errCode==undefined){
							this.$message({
								message: '删除成功',
								type: 'success'
							})
							this.getmemberlist() /*重新获取会员列表*/
						}else{
							this.$message({
								message: data.data.errMsg,
								type: 'warning'
							})
							this.getmemberlist()
						}
					})

				}).catch(() => {

				});
			},
			merchantId(){
				this.show_merchantId=true
			}
		},
		created() {
			this.getmemberlist();
		}
	}
</script>

<style scoped>
#map{
	width: 600px;
	height: 400px;
}
.show_merchantId{
	width: 50%;
	height: 200px;
	background-color: white;
	border: 1px solid #bfcbd9;
	border-radius: 5px;
	margin-left: 100px;
	position: absolute;
	top: 100px;
	z-index: 99;
}
</style>


