<template>
	<el-form :rules="rules2" :model="ruleForm2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
		<h3 class="title" style="font-size: 20px;">拾谷鸟后台管理系统</h3>
		<el-form-item prop="user">
			<el-input type="text" auto-complete="off" placeholder="账号/邮箱/手机号码" v-model='ruleForm2.userName'></el-input>
		</el-form-item>
		<el-form-item prop="password">
			<el-input type="password" v-model="ruleForm2.password" auto-complete="off" placeholder="密码"></el-input>
		</el-form-item>
		<!--<el-form-item prop="password" class="yzmc" v-show="false">
			<el-input type="text" v-model="input" placeholder="输入验证码" style="width: 40%;margin-top: 8px;"></el-input>
			<el-button class="yanzhengma" style="width: 50%;height: 46px;float: right;"></el-button>
		</el-form-item>-->
		<el-checkbox v-model="ruleForm2.isRemember" class="remember">记住密码</el-checkbox>
		<el-form-item style="width:100%;">
			<el-button type="primary" style="width:100%;" @click='login()' :loading="logining">登录</el-button>
		</el-form-item>
		<el-form-item>
		</el-form-item>
	</el-form>
</template>

<script>
	import config from '../config.js'
	export default {
		data() {
			return {
//				input: '',
				logining: false,
				ruleForm2: {
					userName: '',
					password: '',
					clientType: '商家管理后台',
					isRemember: false
				},
				rules2: {
					userName: [{
						required: true,
						message: '请输入账号',
						trigger: 'blur'
					}, ],
					password: [{
						required: true,
						message: '请输入密码',
						trigger: 'blur'
					}, ]
				}
			};
		},
		methods: {
			login() {
				//  	this.$router.push({ path: '/' })
				var sha1 = require('sha1');
				/*随机字符串*/
				
				this.$refs.ruleForm2.validate((valid) => {
					if(valid) {
						this.logining = true;
						let url = config.apiUrl+'/user/login'
						this.$http.post(url, this.ruleForm2)
							.then((data) => {
								if(data.data.errCode == undefined) {
									localStorage.sguuser=JSON.stringify(this.ruleForm2)
									localStorage.loginSession = JSON.stringify(data.data);
									this.$router.push({
										path: '/'
									})
								} else{
									alert(data.data.errMsg)
									this.logining = false;
								}
							})
					}
				})
			}
		}, 
		created() {
			if(localStorage.sguuser!=undefined||localStorage.sguuser!=''){
				var sguuser=JSON.parse(localStorage.sguuser)
				if(sguuser&&sguuser!=''){
					this.ruleForm2.userName=sguuser.userName
					this.ruleForm2.password=sguuser.password
				}else{
					return
				}
			}
		}
	}
</script>
<style>
	.login-container {
		/*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
		-webkit-border-radius: 5px;
		border-radius: 5px;
		-moz-border-radius: 5px;
		background-clip: padding-box;
		margin: 180px auto;
		width: 350px;
		padding: 35px 35px 15px 35px;
		background: #fff;
		border: 1px solid #eaeaea;
		box-shadow: 0 0 25px #cac6c6;
	}
	
	.login-container .title {
		margin: 0px auto 40px auto;
		text-align: center;
		color: #505458;
	}
	
	.login-container .remember {
		margin: 0px 0px 15px 0px;
	}
	
	.verificationcode {
		width: 200px;
		height: 50px;
		border: 1px solid lightgray;
		float: right;
		display: block;
	}
	
	.yanzhengma {
		background: url(../assets/pay_logo.png) no-repeat;
		background-size: 190px;
	}
	
	.yzmc {
		margin: 0;
	}
</style>


