<template>
	<section style="font-size: 16px;">
			<el-form :inline="true" :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<el-form-item label="机器ID" prop="machineNo">
					<el-input v-model="editForm.machineNo"></el-input>
				</el-form-item>
				<el-form-item label="硬件编号" prop="hardwareNo">
					<el-input v-model="editForm.hardwareNo" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="机器名" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="机器型号" prop="model">
					<el-input v-model="editForm.model" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="省份" prop="province">
					<el-input v-model="editForm.province" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="城市" prop="city">
					<el-input v-model="editForm.city" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="镇" prop="town">
					<el-input v-model="editForm.town" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="地址" prop="address">
					<el-input v-model="editForm.address" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="余量警戒线" prop="goodsStockWarning">
					<el-input v-model="editForm.goodsStockWarning" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="谷库存最满量" prop="goodsStockFull">
					<el-input v-model="editForm.goodsStockFull" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="包装袋余量" prop="packageStock">
					<el-input v-model="editForm.packageStock" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="包装袋警戒线" prop="packageStockWarning">
					<el-input v-model="editForm.packageStockWarning" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="包装袋最大值" prop="packageStockFull">
					<el-input v-model="editForm.packageStockFull" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="机器状态" prop="status">
					<el-select v-model="editForm.status" placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :key="item.status"
					      :label="item.value"
					      :value="item.status">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="商家" prop="merchantId">
					<el-select v-model="editForm.merchantId" placeholder="请选择">
					    <el-option
					      v-for="item in options1"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="机器位置">
					<el-button @click="openmap()">选择机器位置</el-button>
					<!--<input type="text" v-show="mapshow" /><button>搜索</button>-->
					<span>经度：{{editForm.lng}}</span> <span>纬度：{{editForm.lat}}</span>
				</el-form-item><br />
				<!--百度地图-->
				<el-input placeholder="输入地点" v-show="mapshow" v-model="mapsearch" style="width: 300px;margin-bottom: 10px;">
					<el-button slot="append" id="mapbtn">搜索</el-button>
				</el-input>
				<div id="map" v-show="mapshow"></div>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">修改</el-button>
				<el-button @click="quxiao()">取消</el-button>
			</div>
		</section>
</template>

<script>
	import {header} from '../../apis/header.js'
	import config from '../../config.js'
	export default {
		data() {
			return {
				editForm: {
					"machineNo": "",
					"hardwareNo": "",
					"name": "",
					"model": "",
					"lng": '',
					"lat": '',
					"province": "",
					"city": "",
					"town": "",
					"address": "",
					"goodsStock": '',
					"goodsStockWarning": '',
					"goodsStockFull": '',
					"packageStock": '',
					"packageStockWarning": '',
					"packageStockFull": '',
					"productTime": "",
					"status": '',
					"merchantId": "",
					"goods": ''
				},
				mapshow: false,
				mapsearch:'',
				options:[{'status':-1,'value':'停机'},{'status':0,'value':'离线'},{'status':1,'value':'待机'},{'status':2,'value':'工作中'}],
				options1:[],
				editFormRules: { /*修改时必填的项目*/
					name: [{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					}]
				},
				editLoading: false,
				machineNo:''
			}
		},
		methods: {
			openmap(){
				let that=this
				this.mapshow=true
				let mapbtn = document.getElementById('mapbtn')
				let map = document.getElementById('map')
				map = new BMap.Map("map")
				map.centerAndZoom(new BMap.Point(113.943214,22.529155),17);
				map.enableScrollWheelZoom();
			    map.enableKeyboard();
			    map.enableDragging();
			    map.enableDoubleClickZoom()
			    map.addEventListener('click',function(e){
			    	that.editForm.lng=e.point.lng
			    	that.editForm.lat=e.point.lat
			    })
			    let local = new BMap.LocalSearch(map, {
					renderOptions:{map: map}
				})
			    mapbtn.addEventListener('click',function(){
			    	if(that.mapsearch!=''){
					  local.search(that.mapsearch)
					}
			    })
			},
			/*获取路有参数*/
			getParams () {
		        this.machineNo = this.$route.query.machineNo
			},
			/*获取机器信息*/
			handleEdit: function(machineNo) {
				let url = config.apiUrl+'/machine/mg/' + machineNo
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						console.log(data.data)
						this.editForm = data.data
					})
			},
			getoptions1(){
				let url=config.apiUrl+'/merchant/list?page=1&pageSize=100&deleted=false'
				this.$http({
					mehtod:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.options1=data.data.list
				})
			},
			editSubmit(){
				this.$confirm('确认修改本机器吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url=config.apiUrl+'/machine'
					this.$http({
						method:'put',
						url:url,
						data:this.editForm,
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then(data=>{
						if(data.data.errCode==undefined){
							this.$alert('修改机器成功', '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									path: '/list'
								})
					          }
					        });
						}else{
							this.$alert(data.data.errMsg, '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									
								})
					          }
					        });
						}
					})
				}).catch(() => {
					return
				});
			},
			quxiao(){
				this.$alert('取消修改', '提示', {
				    confirmButtonText: '确定',
				    callback: action => {
				        this.$router.push({
							path: '/list'
						})
				    }
				});
			}
		},
		computed: {
			
		},
		created(){
            this.handleEdit(this.$route.query.machineNo)
            this.getoptions1()
       },
		watch: {
		// 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
		    '$route': 'getParams'
		}
	}
</script>

<style lang="less" scoped="scoped">
#map{
	width: 600px;
	height: 400px;
}
.associatedGoods{
	width: 100%;
	span{
		display: inline-block;
		width: 33.33%;
		float: left;
	}
}
.dialog-footer{
	float: left;
}
</style>


