<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="ID"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="userlist" highlight-current-row v-loading="listLoading" @selection-change="selsChange">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" width="80">
			</el-table-column>
			<el-table-column prop="id" label="ID" width="100" sortable>
			</el-table-column>
			<el-table-column label="图片" width="120">
			  <template slot-scope="scope" width="120">
			    <img :src= "configUrl+scope.row.photo[0].imgUrl" width="100" height="100"/>
			  </template>
			</el-table-column>
			<el-table-column prop="name" label="类别" min-width="100">
			</el-table-column>
			<el-table-column prop="title" label="产品名" min-width="100">
			</el-table-column>
			<el-table-column prop="model" label="型号" width="120">
			</el-table-column>
			<el-table-column prop="price" label="价格" width="120" sortable>
			</el-table-column>
			<el-table-column prop="netWeight" label="库存" width="100" sortablealign='center'>
			</el-table-column>
			<el-table-column prop="grossWeight" label="销量" width="100" sortablealign='center'>
			</el-table-column>
			<el-table-column label="操作" min-width="200" align='center'>
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!--编辑界面-->
		<el-dialog title="编辑" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="ID" prop="id">
					<el-input v-model="editForm.id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="图片" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="类别" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="产品名" prop="title">
					<el-input v-model="editForm.title" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="型号" prop="model">
					<el-input v-model="editForm.model"></el-input>
				</el-form-item>
				<el-form-item label="价格" prop="price">
					<el-input v-model="editForm.price"></el-input>
				</el-form-item>
				<el-form-item label="库存" prop="netWeight">
					<el-input v-model="editForm.netWeight"></el-input>
				</el-form-item>
				<el-form-item label="销量" prop="grossWeight">
					<el-input v-model="editForm.grossWeight"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--新增界面-->
		<el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="ID" prop="id">
					<el-input v-model="addForm.id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="类别ID" prop="categoryId">
					<el-input v-model="addForm.categoryId" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="商品名" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="商品标题" prop="title">
					<el-input v-model="addForm.title" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="商品型号" prop="model">
					<el-input v-model="addForm.model" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="商品价格" prop="price">
					<el-input v-model="addForm.price" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="市场价格" prop="marketPrice ">
					<el-input v-model="addForm.marketPrice" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="商品简介" prop="shortContent">
					<el-input v-model="addForm.shortContent " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="商品介绍" prop="content">
					<el-input v-model="addForm.content " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="关键词" prop="metaKeyword">
					<el-input v-model="addForm.metaKeyword " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="关键描述" prop="metaDescription">
					<el-input v-model="addForm.metaDescription " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="净重" prop="netWeight">
					<el-input v-model="addForm.netWeight " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="毛重" prop="grossWeight">
					<el-input v-model="addForm.grossWeight " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="上架时间" prop="saleTime">
					<el-input v-model="addForm.saleTime " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="商户ID" prop="merchantId">
					<el-input v-model="addForm.merchantId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form :model="addForm.photo[0]" label-width="80px" >
					<el-form-item label="图片ID" prop="id">
						<el-input v-model="addForm.photo[0].id" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="图片路径" prop="imgUrl">
						<el-input v-model="addForm.photo[0].imgUrl" auto-complete="off"></el-input>
					</el-form-item>
					
				</el-form>
				<el-upload
				  class="upload-demo"
				  action="http://api-dev.sgu6.com/photo"
				  :on-preview="handlePreview"
				  :on-remove="handleRemove"
				  multiple
				  :limit="1"
				  :on-exceed="handleExceed"
				  :file-list="fileList">
				  <el-button size="small" type="primary">点击上传</el-button>
				  <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
				</el-upload>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	export default {
		data() {
			return {
				fileList: [],
				configUrl: 'http://www.sgu6.com',
				filters: {
					name: ''
				},
				userlist: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
				  "id": "",
				  "categoryId": "",
				  "name": "",
				  "title": "",
				  "model": "",
				  "price": 0,
				  "marketPrice": 0,
				  "shortContent": "",
				  "content": "",
				  "metaKeyword": "",
				  "metaDescription": "",
				  "netWeight": 0,
				  "grossWeight": 0,
				  "saleTime": new Date().toLocaleString(),
				  "merchantId": "",
				  "photo": [
				    {
				      "id": "",
				      "imgUrl": "",
				      "sort": 0,
				      "show": false
				    }
				  ],
				  "new": false,
				  "enable": false,
				  "hot": false
				},

				addFormVisible: false,//新增界面是否显示
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//新增界面数据
				addForm: {
				  "id": "",
				  "categoryId": "",
				  "name": "",
				  "title": "",
				  "model": "",
				  "price": 0,
				  "marketPrice": 0,
				  "shortContent": "",
				  "content": "",
				  "metaKeyword": "",
				  "metaDescription": "",
				  "netWeight": 0,
				  "grossWeight": 0,
				  "saleTime": new Date().toLocaleString(),
				  "merchantId": "",
				  "photo": [
				    {
				      "id": "",
				      "imgUrl": "",
				      "sort": 0,
				      "show": false
				    }
				  ],
				  "new": false,
				  "enable": false,
				  "hot": false
				}

			}
		},
		methods: {
			handleRemove(file, fileList) {
	          console.log(file, fileList);
	        },
	        handlePreview(file) {
	          console.log(file);
	        },
	        handleExceed(files, fileList) {
	          this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件`);
	        },
			//性别显示转换
			formatSex: function (row, column) {
				return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取商品列表
			getUsers() {
				let url='http://api-dev.sgu6.com/goods/list?page='+this.page+'&pageSize=10'
				this.$http.get(url)
				.then((data)=>{
					this.userlist=data.data.list
					this.total=data.data.total
				})
			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//显示编辑界面
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
				  "id": "",
				  "categoryId": "",
				  "name": "",
				  "title": "",
				  "model": "",
				  "price": 0,
				  "marketPrice": 0,
				  "shortContent": "",
				  "content": "",
				  "metaKeyword": "",
				  "metaDescription": "",
				  "netWeight": 0,
				  "grossWeight": 0,
				  "saleTime": new Date().toLocaleString(),
				  "merchantId": "",
				  "photo": [
				    {
				      "id": "",
				      "imgUrl": "",
				      "sort": 0,
				      "show": false
				    }
				  ],
				  "new": false,
				  "enable": false,
				  "hot": false
				};
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		created() {
			this.getUsers();
		}
	}

</script>

<style scoped>
</style>


