<template>
	<section style="font-size: 16px;">
			<el-form :inline="true" :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">
				<el-form-item label="机器编号" prop="machineNo">
					<el-input v-model="addForm.machineNo" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="机器名" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="硬件编号" prop="hardwareNo">
					<el-input v-model="addForm.hardwareNo" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="机器型号" prop="model">
					<el-input v-model="addForm.model" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="谷余量" prop="goodsStock">
					<el-input v-model="addForm.goodsStock" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="余量警戒线" prop="goodsStockWarning">
					<el-input v-model="addForm.goodsStockWarning" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="谷库存最满量" prop="goodsStockFull">
					<el-input v-model="addForm.goodsStockFull" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="包装袋余量" prop="packageStock">
					<el-input v-model="addForm.packageStock" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="包装袋警戒线" prop="packageStockWarning">
					<el-input v-model="addForm.packageStockWarning" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="包装袋最大值" prop="packageStockFull">
					<el-input v-model="addForm.packageStockFull" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="商家" prop="merchantId">
					<el-select v-model="addForm.merchantId" placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="机器位置">
					<el-button @click="openmap()">选择机器位置</el-button>
					<!--<input type="text" v-show="mapshow" /><button>搜索</button>-->
					<span>经度：{{addForm.lng}}</span> <span>纬度：{{addForm.lat}}</span>
				</el-form-item><br />
				<!--百度地图-->
				<el-input placeholder="输入地点" v-show="mapshow" v-model="mapsearch" style="width: 300px;margin-bottom: 10px;">
					<el-button slot="append" id="mapbtn">搜索</el-button>
				</el-input>
				<div id="map" v-show="mapshow"></div>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click.native="addmachine()" :loading="addLoading">提交</el-button>
				<el-button @click="quxiao()">取消</el-button>
			</div>
		</section>
</template>

<script>
	import {header} from '../../apis/header.js'
	import config from '../../config.js'
	export default {
		data() {
			return {
				options:[],
				'addForm': {
					"machineNo": "",
					"hardwareNo": "",
					"name": "",
					"model": "",
					"lng": 0,
					"lat": 0,
					"goodsStock": 200,
					"goodsStockWarning": 50,
					"goodsStockFull": 200,
					"packageStock": 1000,
					"packageStockWarning": 100,
					"packageStockFull": 1000,
					"merchantId": ""
				},
				mapshow: false,
				mapsearch:'',
				addLoading: false,
				addFormRules: {
					name: [{
						required: true,
						message: '请输入机器名',
						trigger: 'blur'
					}],
					machineNo:[{
						required: true,
						message: '请输入机器编号',
						trigger: 'blur'
					}],
					merchantId:[{
						required: true,
						message: '请选择商家',
						trigger: 'blur'
					}],
					hardwareNo:[{
						required: true,
						message: '请选择硬件编号',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			openmap(){
				let that=this
				this.mapshow=true
				let mapbtn = document.getElementById('mapbtn')
				let map = document.getElementById('map')
				map = new BMap.Map("map")
				map.centerAndZoom(new BMap.Point(113.943214,22.529155),17);
				map.enableScrollWheelZoom();
			    map.enableKeyboard();
			    map.enableDragging();
			    map.enableDoubleClickZoom()
			    map.addEventListener('click',function(e){
			    	that.addForm.lng=e.point.lng
			    	that.addForm.lat=e.point.lat
			    })
			    let local = new BMap.LocalSearch(map, {
					renderOptions:{map: map}
				})
			    mapbtn.addEventListener('click',function(){
			    	if(that.mapsearch!=''){
					  local.search(that.mapsearch)
					}
			    })
			},
			getoptions(){
				let url=config.apiUrl+'/merchant/list?page=1&pageSize=10'
				this.$http({
					mehtod:'get',
					url:url,
					header: {
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
				})
				.then(data=>{
					this.options=data.data.list
				})
			},
			addmachine(){
				this.$confirm('确认添加此机器吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url=config.apiUrl+'/machine'
					this.$http({
						method:'post',
						url:url,
						data: this.addForm,
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then(data=>{
						if(data.data.errCode==undefined){
							this.$alert('新机器添加成功', '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									path: '/list'
								})
					          }
					        });
						}else{
							this.$alert(data.data.errMsg, '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            
					          }
					        });
						}
					})
				}).catch(() => {
					return
				});
			},
			quxiao(){
				this.$alert('取消添加', '提示', {
				    confirmButtonText: '确定',
				    callback: action => {
				        this.$router.push({
							path: '/list'
						})
				    }
				});
			}
		},
		created() {
			this.getoptions()
		}
	}
</script>

<style scoped="scoped">
#map{
	width: 600px;
	height: 400px;
}
</style>


