<template>
	<div>
		<section v-show='show'>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="keyword" placeholder="名称"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getrole()">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="rolelist" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" width="80">
			</el-table-column>
			<el-table-column prop="name" label="名称" min-width="120" >
			</el-table-column>
			<!--<el-table-column prop="merchantId" label="商家" min-width="150">
				<template loading scope="scope">
			        {{merchant(scope.row.merchantId)}}
			    </template>
			</el-table-column>-->
			<el-table-column prop="type" label="类型" min-width="150">
			</el-table-column>
			<el-table-column prop="createTime" label="创建时间" min-width="150" sortable>
				<template slot-scope="scope">
			        {{timeFormat(scope.row.createTime)}}
			    </template>
			</el-table-column>
			<el-table-column label="操作" width="220" align='center'>
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
	</div>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				keyword:'',
				mapsearch:'',
				mapshow: false,
				shangjiaid: '',
				show: true,
				showadd: false,
				showedit: false,
				show_merchantId: false,
				filters: {
					name: ''
				},
				rolelist: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [], //列表选中列

				editFormVisible: false, //编辑界面是否显示
				editLoading: false,
				editFormRules: { /*修改时必填的项目*/
					name: [{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					}]
				},
				addFormVisible: false, //新增界面是否显示
				addLoading: false,
				addFormRules: {
					name: [{
						required: true,
						message: '请输入机器名',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			handleCurrentChange(val) {
				this.page = val;
				this.getrole();
			},
			//时间戳转化成时间格式
			add0(m) {
				return m < 10 ? '0' + m : m
			},
			timeFormat(timestamp) {
				var time = new Date(timestamp);
				var year = time.getFullYear();
				var month = time.getMonth() + 1;
				var date = time.getDate();
				var hours = time.getHours();
				var minutes = time.getMinutes();
				return year + '.' + this.add0(month) + '.' + this.add0(date) + ' ' + this.add0(hours) + ':' + this.add0(minutes);
			},
			merchant(id){
				let url=config.apiUrl+'/merchant/'+id;
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				}).then((data)=>{
					return data.data.name
				})
			},
			//获取角色列表
			getrole() {
				this.listLoading = true;
				let url =config.apiUrl+'/role/list?page='+this.page+'&pageSize=100'+'&keyword='+this.keyword
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						this.rolelist = data.data.list
						this.total=data.data.total
						this.listLoading = false;
					})
			},
			//删除
			handleDel(index, row) {
				this.$confirm('确认删除本角色吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url = config.apiUrl + '/role'
					this.$http({
						method:'delete',
						url:url,
						data:{"ids":[row.id]},
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then((data) => {
						console.log(data.data.errCode)
						if(data.data.errCode==undefined){
							this.$message({
								message: '删除成功',
								type: 'success'
							})
							this.getrole() /*重新获取机器列表*/
						}else{
							this.$message({
								message: data.data.errMsg,
								type: 'warning'
							})
							this.getrole()
						}
					}).catch((err) => {
						console.log(err)
					})
				}).catch(() => {
					return
				});

			},
			//跳转编辑界面
			handleEdit: function(index, row) {
				this.$router.push({
					path: '/changerole',
					name: '修改角色',
					query: {
						id:row.id
					}
				})
			},
			//跳转新增界面
			handleAdd: function() {
				this.$router.push({
					path: '/addrole'
				})
			},
			//修改
			editSubmit: function() {
				this.$refs.editForm.validate((valid) => {
					if(valid) {
						this.$confirm('确认修改吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let url = config.apiUrl + '/machine'
							this.$http.put(url, this.editForm)
								.then(() => {
									this.$message({
										message: '修改成功',
										type: 'success'
									})
									this.getrole()
								})
						});
					}
				});
			},
			//新增
			addSubmit() {
				/*let url=config.apiUrl+'/machine'
				this.$http.post(url,this.addForm)
				  .then((response)=>{
				  	console.log(response)
				  })*/
				this.$confirm('确认添加吗?', '提示', {
					type: 'warning'
				}).then(() => {
			    		let url = config.apiUrl + '/machine'
					this.$http.post(url, this.addForm).then(() => {
						this.$message({
							message: '添加成功',
							type: 'success'
						})
						this.addFormVisible = false;
						this.getrole() /*重新获取机器列表*/
					}).catch((err) => {
						console.log('错误')
					})
				}).catch(() => {
					return
				});
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function() {
				var ids = this.sels.map(item => item.id );
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					console.log(para)
					let url = config.apiUrl + '/role'
					this.$http({
						method: 'delete',
						url: url,
						data: para,
						header: {
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then((data) => {
						console.log(data)
						if(data.data.errCode==undefined){
							this.$message({
								message: '删除成功',
								type: 'success'
							})
							this.getrole() /*重新获取角色列表*/
						}else{
							this.$message({
								message: data.data.errMsg,
								type: 'warning'
							})
							this.getrole() /*重新获取角色列表*/
						}
					})

				}).catch(() => {

				});
			},
			openmap(){
				let that=this
				this.mapshow=true
				let mapbtn = document.getElementById('mapbtn')
				let map = document.getElementById('map')
				map = new BMap.Map("map")
				map.centerAndZoom(new BMap.Point(113.943214,22.529155),17);
				map.enableScrollWheelZoom();
			    map.enableKeyboard();
			    map.enableDragging();
			    map.enableDoubleClickZoom()
			    map.addEventListener('click',function(e){
			    	that.addForm.lng=e.point.lng
			    	that.addForm.lat=e.point.lat
			    })
			    let local = new BMap.LocalSearch(map, {
					renderOptions:{map: map}
				})
			    mapbtn.addEventListener('click',function(){
			    	if(that.mapsearch!=''){
					  local.search(that.mapsearch)
					}
			    })
			},
			merchantId(){
				this.show_merchantId=true
			}
		},
		created() {
			this.getrole();
		}
	}
</script>

<style scoped>
#map{
	width: 600px;
	height: 400px;
}
.show_merchantId{
	width: 50%;
	height: 200px;
	background-color: white;
	border: 1px solid #bfcbd9;
	border-radius: 5px;
	margin-left: 100px;
	position: absolute;
	top: 100px;
	z-index: 99;
}
</style>


