<template>
	<section style="font-size: 16px;">
			<el-form :inline="true" :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<!--<el-form-item label="ID" prop="id">
					<el-input v-model="editForm.id" :disabled="true"></el-input>
				</el-form-item>-->
				<el-form-item label="用户名" prop="username">
					<el-input v-model="editForm.username"></el-input>
				</el-form-item>
				<el-form-item label="实名" prop="realname">
					<el-input v-model="editForm.realname" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="账户状态" prop="status">
					<el-select v-model="editForm.status" placeholder="请选择">
					    <el-option
					      v-for="item in options2"
					      :key="item.status"
					      :label="item.value"
					      :value="item.status">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="QQ" prop="qq">
					<el-input v-model="editForm.qq" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="手机号码" prop="phone">
					<el-input v-model="editForm.phone" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
					<el-input v-model="editForm.email" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="支付宝账号" prop="buyerLogonId ">
					<el-input v-model="editForm.buyerLogonId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="微信号" prop="wechatId">
					<el-input v-model="editForm.wechatId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="性别" prop="gender">
					<el-select v-model="editForm.gender" placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :key="item.status"
					      :label="item.value"
					      :value="item.status">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="销售提成" prop="percentage ">
					<el-input v-model="editForm.percentage " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="积分" prop="score">
					<el-input v-model="editForm.score" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="出生日期" prop="birthday">
					<el-date-picker
				      v-model="editForm.birthday"
				      type="date"
				      placeholder="选择日期">
				    </el-date-picker>
				</el-form-item>
				<el-form-item label="账户余额" prop="accountSum">
					<el-input v-model="editForm.accountSum" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="VIP" prop="vip">
					<el-select v-model="editForm.vip" placeholder="请选择">
					    <el-option
					      v-for="item in options1"
					      :key="item.status"
					      :label="item.value"
					      :value="item.status">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="米余量" prop="riceSum ">
					<el-input v-model="editForm.riceSum " auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click="editSubmit()" :loading="editLoading">修改</el-button>
				<el-button @click="quxiao()">取消</el-button>
			</div>
		</section>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				options:[{'status':0,'value':'女'},{'status':1,'value':'男'}],
				options1:[{'status':0,'value':'不是'},{'status':1,'value':'是'}],
				options2:[{'status':0,'value':'禁用'},{'status':1,'value':'正常'}],
				editForm: {},
				editFormRules: { /*修改时必填的项目*/
					loginName: [{
						required: true,
						message: '请输入用户名',
						trigger: 'blur'
					}]
				},
				editLoading: false,
				id:''
			}
		},
		methods: {
			/*获取路有参数*/
			getParams () {
		        this.id = this.$route.query.id
			},
			/*获取用户信息*/
			handleEdit(id) {
				let url = config.apiUrl+'/member/' + id
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						console.log(data.data)
						this.editForm = data.data
					})
			},
			editSubmit(){
				this.$confirm('确认修改此会员吗?', '提示', {
					type: 'warning'
				}).then(() => {
					let url=config.apiUrl+'/member'
					this.$http({
						method:'put',
						url:url,
						data:this.editForm,
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then(data=>{
						if(data.data.errCode==undefined){
							this.$alert('修改成功', '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									path: '/memberlist'
								})
					          }
					        });
						}else{
							this.$alert(data.data.errMsg, '提示', {
					          confirmButtonText: '确定',
					          callback: action => {
					            this.$router.push({
									
								})
					          }
					        });
						}
					})
				}).catch(() => {
					return
				});
			},
			quxiao(){
				this.$alert('取消修改', '提示', {
				    confirmButtonText: '确定',
				    callback: action => {
				        this.$router.push({
							path: '/memberlist'
						})
				    }
				});
			}
		},
		computed: {
			
		},
		created(){
            this.handleEdit(this.$route.query.id)
       },
		watch: {
		// 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
		    '$route': 'getParams'
		}
	}
</script>

<style>
</style>


